<template>
    <div class="NewAddress_box">
         <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">新增地址</p>
            <div slot="last"></div>
        </Header>

        <div class="information">
            <p>
                <input type="text" placeholder="请填写你的姓名" v-model="name">
                <span v-if="showna">请输入姓名</span>
            </p>
            <p @click="school()">
                <input type="text" placeholder="小区/写字楼/学校等" v-model="xue"/>
            </p>
            <p>
                <input type="text" placeholder="请填写详细送餐地址" v-model="dizhi">
                <span v-if="show">送餐地址太短，不能辨识</span>
            </p>
            <p>
                <input type="text" placeholder="请填写能够联系到您的手机号" v-model="Mobile">
                <span v-if="showble">请输入正确的手机号</span>
            </p>
            <p>
                <input type="text" placeholder="备用联系电话(选填)" v-model="Liphone">
                <span v-if="showphone">请输入正确的手机号</span>
            </p>
        </div>

        <div class="newbutton" @click="NewAdd()">
            <input type="button" value="新增地址">
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
import { Dialog } from 'vant';
export default {
    components:{
        Header
    },
    data() {
        return {
            name:"",
            dizhi:"",
            Mobile:"",
            Liphone:"",
            show:false,
            showble:false,
            showphone:false,
            showna:false,
            inputval:"",
            xue:""
        }
    },
    created() {
        // 获取搜索地址页的地址信息
            // console.log(this.$route.query.name)
            //this.xue=this.$route.query.name
    },
    methods: {
        school(){
            // 选择地址跳转填写新增地址
            this.$router.push("/home/searaddre")
            this.$route.query.name
        },
        NewAdd(){
            
            if(this.dizhi==""||this.Mobile==""||this.Liphone==""||this.name==""){
                Dialog.alert({
                    message: '地址信息错误',
                    }).then(() => {
                    
                    })
            this.axios.post("http://elm.cangdu.org/v1/users/38150/addresses",{
                address: this.xue,
                address_detail: this.dizhi,
                geohash: "31.22299,121.36025",
                name: this.name,
                phone: this.Mobile,
                phone_bk: this.Liphone,
                poi_type: 0,
                sex: 1,
                tag: "公司",
                tag_type: 4,
                }).then((res)=>{
                    
            })
            }else{
                this.$router.push("/home/shiaddress")
            }
        }
    },
    watch: {
        $route(v){
            this.xue=v.query.name
        },
        //姓名
        name(){
            if(this.name==""){
                this.showna=true
            }else{
                this.showna=false
            }
        },
        //    地址
        dizhi(v){
            if(v.length<3||""){
                this.show=true;
            }else{
                this.show=false
            }
        },
        //    手机
        Mobile(v){
            if(v.length<11){
                this.showble=true;
            }else{
                this.showble=false
            }
        },
        // 备用
        Liphone(v){
            if(v.length<11){
                this.showphone=true;
            }else{
                this.showphone=false
            }
        }
    }
}
</script>

<style lang="scss">
.NewAddress_box{
    width: 100%;
    .information{
        width: 100%;
        margin-top:1rem;
        background: white;
        p{
            width: 100%;
            font-size: 0.3rem;
            text-align: center;
            padding: 0.2rem 0;
            span{
                display: block;
                text-align: left;
                padding-left: 0.3rem;
                color: red;
            }
            input{
                line-height:0.65rem;
                width: 90%;
                background: rgb(248, 247, 247);
                padding-left: 0.15rem;
                border: 1px solid #cccccc;
                border-radius: 0.1rem;
            }
        }
    }
    .newbutton{
        width: 100%;
        margin-top: 0.25rem;
        input{
            width:95%;
            height: 0.8rem;
            font-size: 0.4rem;
            text-align: center;
            margin-left: 0.15rem;
            background: #4CD964;
            border: none;
            border-radius: 0.08rem;
            color: white;
        }
    }
}

</style>


